body {
    margin: 0;
    padding: 0;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: large;
    background-color: black;
    color: white;
    
}


.margin-pack {
    margin: 2%;
    background: rgb(63,94,251);
    background: -webkit-radial-gradient(circle, rgba(63,94,251,1) 0%, rgba(88,212,212,1) 27%, rgba(252,70,107,1) 100%);
    background: -o-radial-gradient(circle, rgba(63,94,251,1) 0%, rgba(88,212,212,1) 27%, rgba(252,70,107,1) 100%);
    background: radial-gradient(circle, rgba(63,94,251,1) 0%, rgba(88,212,212,1) 27%, rgba(252,70,107,1) 100%);
}



.margin-tb-pack {
    margin: 3%;
}

.flex-box {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
background: black;

}


.pack {
    width: 98%;
    margin: 1% 1% 3%;
}

@media only screen and (min-width: 768px) {
    .pack {
        width: 31%;
        margin: 1%;

    }
} 

.edit {
    background-color: yellow;
    margin-right: 200px;
    
}

.title {
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    margin-left: 400px;
    background-color: #212121;
  color: #4db1bc;
  margin: 0;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  -ms-flex-align: center;
      align-items: center;
  justify-items: center;
  -webkit-animation: glow 2s ease-in-out infinite;
          animation: glow 2s ease-in-out infinite 
  
}


